
.time-entries{
	width: 100%;
}
.list-group-item{
	width: 90%;
	height: 170px;
	margin:20px auto;
	background-color: #fff;
	border: 2px solid #ccc;
	border-radius: 10px;
	position: relative;
	margin-bottom: 40px;
}
.user-details{
	float: left;
	width: 170px;
	height: 170px;
	background-color: #eee;
	border-radius: 11px 0 0 11px;
	text-align: center;
}
.avatar{
	width: 110px;
	height: 110px;
	padding: 10px;
	border-radius: 100%;
}
.time-block{
	padding: 20px;

	text-align: center;
	float: left;
}
.total-time{
	font-size: 2em;
	padding: 20px;
	margin-left: 40px;
	margin-right: 60px;
}
.calendar-time{
	font-size: 1.2em;
	padding: 10px;
}
.comment-section{
	padding: 20px;
	padding-top: 40px;
}

.deletBtn{
	position: absolute;
	right: 7px;
	top: 5px;
	width: 1.2em;
	height: 1em;
	padding: 10px;
	text-align: center;
	border-radius: 5px;
	background-color: #fff;
	-webkit-transition:  0.4s;
	transition:  0.4s;
}

.deletBtn:hover{
  cursor: pointer;
  color: #fff;
  background-color: #F45551;
}
.btn-create{
  width: 300px;
	padding: 10px 80px;
	text-align: center;
	border-radius: 5px;
  box-sizing:border-box;
  -moz-box-sizing:border-box; /* Firefox */
  -webkit-box-sizing:border-box;
	background-color: #4CB0F9;
	color: #fff;
	-webkit-transition:  0.4s;
	transition:  0.4s;
	font-size: 1.5em;
	cursor: pointer;
  margin-left: 8.66%;
  margin-top: 20px;
}
.btn-create:hover {
	background-color: #1B9AF7;
}
.no-task{
  width: 80%;
  margin:20px auto;
  padding: 20px;
  background-color: #F27A7A;
  border-radius: 5px;
}



